.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  z-index: 1000;

  min-width: 450px;
  min-height: 609px;

  background-color: var(--primary-bg-color);
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 4px;

  @media screen and (min-width: 1200px) {
    min-width: 528px;
    min-height: 581px;
  }
}

.modal-btn {
  position: absolute;
  top: 0;
  right: 0;

  margin-top: 8px;
  margin-right: 8px;

  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  padding: 10px;

  background-color: var(--primary-bg-color);
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  cursor: pointer;
}

.close-btn {
  fill: var(--header-logo-color);

  transition: fill var(--anim);
}

.modal-btn:hover .close-btn,
.modal-btn:focus .close-btn {
  fill: var(--accent-color);
}

.modal-form-area {
  display: block;
  padding: 40px;
}

.modal-title {
  margin-bottom: 12px;

  font-weight: 700;
  font-size: 20px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.03em;
  color: var(--title-text-color);

  @media screen and (min-width: 1200px) {
    line-height: 1.5;
  }
}

.form-list-item {
  position: relative;

  &:not(:last-child) {
    margin-bottom: 10px;
  }

  &:last-child {
    margin-bottom: 20px;
  }
}

.label-text {
  font-size: 12px;
  line-height: 1.17;
  letter-spacing: 0.01em;
  margin-bottom: 4px;
}

.modal-form-field {
  width: 100%;
  height: 40px;
  padding: 11px 12px 11px 42px;
  font-size: 14px;
  line-height: 1.4;
  letter-spacing: 0.01em;
  color: var(--title-text-color);
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;

  transition: outline var(--anim), border-color var(--anim);

  @media screen and (min-width: 1200px) {
    padding: 12px 15px 12px 42px;
    font-size: 16px;
    line-height: 1.25;
    letter-spacing: 0.03em;
  }
}

.modal-form-field-comment {
  width: 100%;
  height: 120px;
  padding: 12px 16px;
  color: var(--title-text-color);
  background-color: transparent;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;

  transition: outline var(--anim), border-color var(--anim);
}

textarea {
  resize: none;
}

.modal-form-field-comment::placeholder {
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.01em;
  color: rgba(117, 117, 117, 0.5);

  @media screen and (min-width: 1200px) {
    font-size: 12px;
    line-height: 1.7;
  }
}

.modal-form-field:focus,
.modal-form-field-comment:focus {
  outline: transparent;
  border-color: var(--accent-color);
}

.modal-form-icon {
  position: absolute;
  top: 55%;
  left: 12px;
  fill: var(--title-text-color);
}

.modal-form-field:focus+.modal-form-icon {
  fill: var(--accent-color);
}

.modal-form-checkbox {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 30px;
}

.form-icon-check {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 16px;
  height: 15px;
  margin-right: 8px;
  outline: 2px solid var(--title-text-color);
  outline-offset: -2px;
  border-radius: 2px;

  transition: background-color var(--anim), outline-color var(--anim);
}

.modal-form-check:checked+.form-icon-check {
  background-color: var(--accent-color);
  outline-color: var(--accent-color);
}

.icon-check {
  fill: var(--primary-bg-color);
}

.checkbox-text {
  font-size: 12px;
  line-height: 1.7px;
  letter-spacing: 0.03em;
  color: var(--main-text-color);

  @media screen and (min-width: 1200px) {
    font-size: 14px;
    line-height: 1.71;
  }
}

.agreement {
  text-decoration-line: underline;
  color: var(--accent-color);
}

.btn-checkbox {
  margin-right: 124px;
  margin-left: 124px;

  @media screen and (min-width: 1200px) {
    margin-right: 164px;
    margin-left: 164px;
  }
}